import { mapGetters } from 'vuex';
import Moment from 'moment';
import icons from '../icons';

export default {
  name: 'ProxyCard',
  props: {
    proxyCardInfo: {
      type: Object,
      default: () => {},
    },
  },
  computed: {
    ...mapGetters('config', ['siteOwner']),
    ...mapGetters('profile', ['profile']),
    currentDate() {
      return Moment().format('Do MMMM YYYY');
    },
    icons() {
      return icons;
    },
  },
};
